<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Employees</h4></li>
      <li v-for="emp in employee" :key="emp.id" class="collection-item">
        <div class="chip">{{ emp.dept }}</div>
        {{ emp.id }}:{{ emp.name }}
        <router-link
          v-bind:to="{ name: 'view-employee', params: { id: emp.id } }"
          class="secondary-content"
        >
        <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import firebase from "../plugins/firebaseConfig";
const db = firebase.firestore();

export default {
  name: "dashboard",
  data() {
    return {
      employee: [],
      employeesData: [],
    };
  },
  created() {
    db.collection("employees")
      .orderBy("name")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            fid: doc.id,
            id: doc.data().id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position,
          };
          this.employee.push(data);
        });
      });

    // this.employeesData = [];
    // db.collection("employees")
    //   .get()
    //   .then((querySnapshot) => {
    //     querySnapshot.forEach((doc) => {
    //       this.employeesData.push({
    //         id: doc.id,
    //         name: doc.name,
    //         dept: doc.dept,
    //         position: doc.position,
    //       });
    //       console.log(doc.id, " => ", doc.data());
    //     });
    //   })
    //   .catch((error) => {
    //     console.log("Error getting documents: ", error);
    //   });
  },
};
</script>

